import React, { Component } from 'react';
import Sample from './Sample';
import './App.css';

class App extends Component {
  render() {

    const fonts = ['Aldrich', 'Amiko', 'Chivo', 'Didact Gothic', 'Encode Sans Semi Condensed', 'Exo', 'Khula', 'Lato', 'Montserrat', 'Muli', 'NTR', 'Nanum Gothic', 'Nunito Sans', 'Orbitron', 'Play', 'Poppins', 'Questrial', 'Rajdhani', 'Reem Kufi', 'Rubik', 'Saira', 'Snippet', 'Varela'];
    const fontWeights = [{regular:'normal',x:'bold'}, {regular:'bold',x:'bolder'}]
    const xStyle = ['lowercase','uppercase']

  const samples = fonts.map( f => {
    return fontWeights.map( w => {
      return xStyle.map( s => {
        return <Sample fontName={f} weights={w} xstyle={s} key={f+w.regular+s}/>
        });
      });
    });
    return (
      <div className="App">
        {samples}
      </div>
    );
  }
}

export default App;
